.profile {
  text-align: center;
  width: 300px;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
  background-color: rgba(255, 235, 205, 0.959);
  font-family: Arial, Helvetica, sans-serif;
  border-radius: 5px;
  box-shadow: 2px 2px 2px 1px rgba(136, 133, 133, 0.2);
}

.description {
  padding-top: 30px;
}

.avatar {
  display: block;
  width: 80px;
  height: 80px;
  margin-left: auto;
  margin-right: auto;
  border-radius: 50%;
}

.name {
  margin-bottom: 0;
  font-size: 25px;
  font-weight: bold;
}

.tag,
.location,
.label {
  color: rgb(124, 121, 121);
  display: block;
}

.stats {
  display: flex;
  padding: 0;
  margin-top: 40px;

  border-radius: 5px;
  background-color: rgb(231, 231, 240);
}

li {
  display: flex;
  flex-direction: column;
  justify-content: center;
  list-style-type: none;
  width: 100px;
  height: 100px;
  border-top: 1px solid rgb(185, 185, 204);
}

li:not(:last-child) {
  border-right: 1px solid rgb(185, 185, 204);
}

.label {
  font-size: 12px;
}

.quantity {
  margin-top: 5px;
  font-weight: bold;
}
